<template>
  <div class="register-container">
    <!-- 注册内容 -->
    <div class="register">
      <h3>
        注册新用户
        <span class="go"
          >我有账号，去 <a href="login.html" target="_blank">登录</a>
        </span>
      </h3>
      <ValidationObserver ref="register">
        <div class="content">
          <label>手机号:</label>
          <ValidationProvider
            name="手机号"
            v-slot="{ errors }"
            rules="shouji|required"
          >
            <input v-model="phone" type="text" placeholder="请输入你的手机号" />
            <span class="error-msg">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>
        <div class="content">
          <label>验证码:</label>
          <ValidationProvider
            name="验证码"
            v-slot="{ errors }"
            rules="yanzhengma|required"
          >
            <input v-model="code" type="text" placeholder="请输入验证码" />
            <button class="getcode" @click="getCode">获取验证码</button>
            <span class="error-msg">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>
        <div class="content">
          <label>登录密码:</label>
          <ValidationProvider
            name="密码"
            v-slot="{ errors }"
            rules="mima|required"
          >
            <input
              v-model="password"
              type="text"
              placeholder="请输入你的登录密码"
            />
            <span class="error-msg">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>
        <div class="content">
          <label>确认密码:</label>
          <ValidationProvider
            name="确认密码"
            v-slot="{ errors }"
            :rules="`eqeq:${password},密码|required`"
          >
            <input
              v-model="re_Password"
              type="text"
              placeholder="请输入确认密码"
            />
            <span class="error-msg">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>
        <div class="controls">
          <ValidationProvider name="协议" v-slot="{ errors }" rules="agree">
            <input v-model="agree" name="m1" type="checkbox" />
            <span>同意协议并注册《尚品汇用户协议》</span>
            <span class="error-msg">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>
      </ValidationObserver>
      <div class="btn">
        <button @click="register">完成注册</button>
      </div>
    </div>

    <!-- 底部 -->
    <div class="copyright">
      <ul>
        <li>关于我们</li>
        <li>联系我们</li>
        <li>联系客服</li>
        <li>商家入驻</li>
        <li>营销中心</li>
        <li>手机尚品汇</li>
        <li>销售联盟</li>
        <li>尚品汇社区</li>
      </ul>
      <div class="address">地址：北京市昌平区宏福科技园综合楼6层</div>
      <div class="beian">京ICP备19006430号</div>
    </div>
  </div>
</template>

<script>
import { reqGetCode, reqRegister } from "@/api";
export default {
  name: "Register",
  data() {
    return {
      phone: "",
      password: "",
      code: "",
      re_Password: "",
      agree: false,
    };
  },
  methods: {
    async getCode() {
      let { code, message, data } = await reqGetCode(this.phone);
      if (code === 200) {
        this.$message.success("验证码下发成功！");
        this.code = data;
      } else {
        this.$message.error(`验证码下发失败${message}`);
      }
    },
    async register() {
      const { phone, password, code } = this;
      let {
        code: resCode,
        message,
        data,
      } = await reqRegister({ phone, password, code });

      if (resCode === 200) {
        // 注册成功后给提示
        this.$message.success("注册成功");
        // 引导用户去登录
        this.$router.push("/login");
      } else {
        this.$message.warning(message);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.register-container {
  .register {
    width: 1200px;
    height: 445px;
    border: 1px solid rgb(223, 223, 223);
    margin: 0 auto;

    h3 {
      background: #ececec;
      margin: 0;
      padding: 6px 15px;
      color: #333;
      border-bottom: 1px solid #dfdfdf;
      font-size: 20.04px;
      line-height: 30.06px;

      span {
        font-size: 14px;
        float: right;

        a {
          color: #e1251b;
        }
      }
    }

    div:nth-of-type(1) {
      margin-top: 40px;
    }

    .content {
      padding-left: 390px;
      margin-bottom: 18px;
      position: relative;

      label {
        font-size: 14px;
        width: 96px;
        text-align: right;
        display: inline-block;
      }

      input {
        width: 270px;
        height: 38px;
        padding-left: 8px;
        box-sizing: border-box;
        margin-left: 5px;
        outline: none;
        border: 1px solid #999;
      }

      img {
        vertical-align: sub;
      }

      .error-msg {
        position: absolute;
        top: 100%;
        left: 495px;
        color: red;
      }
    }

    .controls {
      text-align: center;
      position: relative;

      input {
        vertical-align: middle;
      }

      .error-msg {
        position: absolute;
        top: 100%;
        left: 495px;
        color: red;
      }
    }

    .btn {
      text-align: center;
      line-height: 36px;
      margin: 17px 0 0 55px;

      button {
        outline: none;
        width: 270px;
        height: 36px;
        background: #e1251b;
        color: #fff !important;
        display: inline-block;
        font-size: 16px;
      }
    }

    .getcode {
      height: 38px;
      margin-left: 10px;
      padding: 5px;
    }
  }

  .copyright {
    width: 1200px;
    margin: 0 auto;
    text-align: center;
    line-height: 24px;

    ul {
      li {
        display: inline-block;
        border-right: 1px solid #e4e4e4;
        padding: 0 20px;
        margin: 15px 0;
      }
    }
  }
}
</style>